body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

 
*{
  margin:0;
  padding:0;
  box-sizing: content-box;
}

button{

  background:transparent;
}


.container{
  
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height:100vh;
  background:-webkit-gradient( linear, left top, left bottom, from(rgba(0, 0, 0, 0.7)), to(rgba(0, 0, 0, 0.1)) ),
  url(/fav-share/static/media/riri2.1e2f9de6.jpg)  center no-repeat;
  background:linear-gradient( rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.1) ),
  url(/fav-share/static/media/riri2.1e2f9de6.jpg)  center no-repeat;
  object-fit: cover;

}

.container > p{

  color:#fff;
  font-size: 1.3em;

}

.get-started{

  outline:none;
  padding: 10px 20px;
  border: 2px solid #fff;
  color:#fff;
  background:transparent;
  -webkit-transition: padding 0.5s;
  transition: padding 0.5s;
}

.get-started:hover{

  padding: 10px 40px;
}

.main-heading{

  font-size:5em;
  color:rgb(224, 255, 24);
  text-align: center;
}



@media screen and (min-width:320px) {

  .container{

   background-position-x: -1000px;
  
  }

  .container > p{

    font-size:1.1em;
  }

  
}

