@-webkit-keyframes slideUp{

    0% { 

        -webkit-transform: translate(0, 300px); 

                transform: translate(0, 300px);
    }


    100% { 

        -webkit-transform: translate(0, 0); 

                transform: translate(0, 0);
    }


}

@keyframes slideUp{

    0% { 

        -webkit-transform: translate(0, 300px); 

                transform: translate(0, 300px);
    }


    100% { 

        -webkit-transform: translate(0, 0); 

                transform: translate(0, 0);
    }


}

.login-container{

    -webkit-animation: slideUp 1s;

            animation: slideUp 1s;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    margin-top:1em;
    background:#D5F11F;
    max-width: 100%;
    width:500px;
    height:350px;
    opacity:0.8;
    box-shadow: 5px 10px 20px #342c00;
}

.routes{
    align-self: center;
    display:flex;
    flex-direction: row;
}

.routes li{

    list-style: none;
    margin: 0 0.5em;
    font-size:16pt;
    color:#E43c4b;
}

form{

    display:flex;
    flex-direction:column;
}

input[name="email"], input[name="pass"]{

    padding: 15px 15px;
    margin: 8px 0;
    border-radius: 25px;
    border: none;
    width:250px;
    background: rgba(19, 19, 19, 0.098);
    align-self: center;
    box-sizing: border-box;
    text-indent: 40px;
    color:#fff;
    outline:none; 
}

input[type=submit]{

    padding: 15px 15px;
    margin:25px 0;
    border-radius: 25px;
    border: none;
    width:250px;
    align-self: center;
    background:#E43c4b;
    color:#ffffff;
    box-sizing: border-box;
    outline:none; 
  }
  
  input:focus {
    color:#ffffff;
  }
/* body { background-color:rgb(31, 31, 31) } */
p{
    margin-bottom:0.5em;
  }

  a{
    text-decoration: none;
  }

  a:hover{
    text-decoration: underline;
    -webkit-text-decoration-color: #fff;
            text-decoration-color: #fff;
  }

  @-webkit-keyframes press{

    0%{

      box-shadow: 5px 5px 10px rgb(199, 38, 52);
    }

    100%{

    box-shadow:none;


    }
  }

  @keyframes press{

    0%{

      box-shadow: 5px 5px 10px rgb(199, 38, 52);
    }

    100%{

    box-shadow:none;


    }
  }

  input:-webkit-autofill, textarea:-webkit-autofill, select:-webkit-autofill{
    -webkit-transition-delay: 3600s;
            transition-delay: 3600s;
  }
  
  .search-container{
  
    display:flex;
    flex-direction:column;
    align-items: center;

  }
  
  input[name="search"]{
    
    /* for mobile change position for none */
    /* position: fixed; */
    margin-top:2em;
    min-width:50%;
    height:25px;
    border-radius: 20px;
    border:2px solid #F5F5F5;
    text-indent: 10px;
    color:black;
    outline:none;
  
  }

  input[name="search"]:focus{

    border:2px solid #E43C4B;

  }
  
  .album-info-container{
    
    margin-top:2em;
    display:flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content:space-evenly;
  }
  
  .album-info-row{
    display:flex;
    flex-flow: row-wrap;
    margin-bottom:1em;
    width:500px;
    background:rgb(245, 245, 245);
    padding:1em;
  }

  
  
  .album-info{
  
    display:flex;
    flex-direction: column;
    align-self: flex-start;
    padding-left:2em;
    
  }
  
  .placeholder{
  
    align-self: center;
    width:100px;
    height:100px;
  }
  
  .add-lib-btn{

    -webkit-animation-name: press;

            animation-name: press;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
   width:200px;
   outline:none;
   padding:1em;
   border:none;
   color:#fff;
   background-color:#E43c4b;
   /* box-shadow: 5px 5px 10px rgb(199, 38, 52); */
  }

  .add-lib-btn:hover{

    box-shadow:none;
  }

  @media screen and (min-width: 320px) and (max-width:600px){


    .album-info-row{
      flex-flow:column nowrap;
