}

.header {
    display:flex;
    justify-content: flex-end;
    width:100%;
    height:50px;
    background:#E43c4b;
}

.header ul{
    align-self: center;
    display:flex;
    flex-direction:row;
    justify-content: flex-end;

}

.header li{
    list-style-type: none;
    padding-right:0.6em;
    color:#fff;
    text-decoration: none;
}




.collection-container{

    display:flex;
    flex-direction: column;
    /* margin:0.5em 0; */
}

.music-list-row{

   display:flex;
   flex-direction:column;
   margin:0 0.5em;
}

